import loginAPI from '../../api/login'
import * as types from '../types'

const namespaced = true

const state = {
    usuario:"",
    password:""
}

const mutations = {
    [types.SET_USERNAME]:(state, value) => {
        console.log("USUARIO", value)
        state.usuario = value
    },
    [types.SET_PASSWORD]:(state, value) => {
        console.log("PASSWORD", value)
        state.password = value
    },
}

const actions = {
    setUsuario(context, params){
        context.commit(types.SET_USERNAME, params)
    },
    setPassword(context, params){
        context.commit(types.SET_PASSWORD, params)
    },
    login(context){
        const result = loginAPI.login(context.state)
        return result
    }
}

export default {
    namespaced,
    state,
    mutations,
    actions
}