<template>
    <div>
        <div class="login-input">
            <input 
                v-model="usuario"
                type="text" 
                id="usuario"
            />
            <label 
                class="login-label"
                for="usuario"
            >
                Usuario
            </label>
        </div>
        <div class="login-input">
            <input 
                v-model="password"
                type="text" 
                id="password"
            />
            <label 
                class="login-label"
                for="password"
            >
                Contraseña
            </label>
        </div>
        <div>
            <button 
                class="login-button"
                @click="login"
            >
                Iniciar sesión
            </button>
        </div>
    </div>
</template>

<script>

import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers("login")

export default {
    data(){
        return {
            
        }
    },
    computed:{
        ...mapState({
            'usuarioStore':'usuario',
            'passwordStore':'password'
        }),
        usuario:{
            get(){
                return this.usuarioStore
            },
            set(value){
                this.setUsuario(value)
            }
        },
        password:{
            get(){
                return this.passwordStore
            },
            set(value){
                this.setPassword(value)
            }
        }
    },
    methods:{
        ...mapActions([
            'login',
            'setUsuario',
            'setPassword'
        ])
    }
}
</script>

<style>

.login-input{
    margin: 5px;
    padding: 5px;
    border: 1px;
    border-radius: 5px;
    width: min-content;
    background: lightcyan;
}

.login-button {
    width: 100%;
}
</style>
